import React from 'react';
import { Col, Row } from 'react-bootstrap';
import './Home.css';
import logo from '../../img/owner.png'

// 12: have 12 columns work with
//
const Home = (props) => {
    return (
        <Row>
            <Col md={12}>
                <div className={'homeText'}>
                    <p>"WELCOME TO ACCOUNT-OWNER APPLICATION"</p>
                    <br/>
                    <img src={logo} alt="" height="300"></img>
                </div>
            </Col>
        </Row>
    )
}

export default Home;